import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-create-subitems',
  templateUrl: './create-subitems.component.html',
  styleUrls: ['./create-subitems.component.css']
})
export class CreateSubitemsComponent implements OnInit {

  public page: any = {};
  public menu_items: any = [];
  public loading: boolean = false;
  public groups: any = [];
  public items: any = [];
  public subitem: any = {
    group_id: '',
    item_id: '',
    description: ''
  }

  constructor(
    private globalService: GlobalService,
    private adminService: AdminService,
    public router: Router
  ) { }

  ngOnInit() {
    this.page = this.adminService.getAdminRoute();
    this.menu_items = this.adminService.getAdminMenu();

    this.getGroups();
    this.getItems();
  }

  getGroups() {
    this.globalService.getGroups().subscribe((groups: any) => {
      this.groups = groups;
    });
  }

  getItems() {
    this.globalService.getItems().subscribe((items: any) => {
      this.items = items;
    })
  }

  addSubitem() {
    this.loading = true;

    this.globalService.createSubitem(this.subitem).subscribe(data => {
      this.globalService.goBack();

      this.loading = false;
    });
  }

}
