import { Component, OnInit } from '@angular/core';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';
import { GuaranteesService } from 'src/app/Services/guarantees.service';

@Component({
  selector: 'app-create-guarantee-maintenance',
  templateUrl: './create-guarantee-maintenance.component.html',
  styleUrls: ['./create-guarantee-maintenance.component.css']
})
export class CreateGuaranteeMaintenanceComponent implements OnInit {


  public guarantee_maintenance: any = {
    root_guarantee_id: '',
    group_id: '',
    item_id: '',
    activity: '',
    description: '',
    amount: '',
    period: '',
    responsible: '',
    font: ''
  }

  public header: any = {};
  public guarantees: any = [];
  public groups: any = [];
  public items: any = [];
  public subitems: any = [];
  public page: any = {};
  public menu_items: any = [];
  public loading: boolean = false;

  constructor(
    private globalService: GlobalService,
    private adminservice: AdminService,
    private guaranteeService: GuaranteesService
  ) { }

  async ngOnInit() {
    this.page = this.adminservice.getAdminRoute();
    this.menu_items = this.adminservice.getAdminMenu();

    this.header = await this.guaranteeService.getHeaderGuaranteeMaintenance();

    this.getGuarantees();
    this.getGroups();
    this.getItems();
  }

  getGuarantees() {
    this.globalService.getGuarantees().subscribe((guarantees: any) => {
      this.guarantees = guarantees;
    });
  }

  async getGroups() {
    this.globalService.getGroups().subscribe((groups: any) => {
      this.groups = groups;
    });
  }

  getItems() {
    this.globalService.getItems().subscribe((items: any) => {
      this.items = items;
    });
  }

  addGuaranteeMaintenance() {
    this.globalService.createGuaranteeMaintenance(this.guarantee_maintenance).subscribe((data: any) => {
      this.globalService.goBack();
    });
  }

}
