import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-show-maintenances',
  templateUrl: './show-maintenances.component.html',
  styleUrls: ['./show-maintenances.component.css']
})
export class ShowMaintenancesComponent implements OnInit {

  public page: any = {};
  public maintenances: any;
  public menu_items: any = [];
  public loading: boolean = false;

  constructor(
    private globalService: GlobalService,
    private adminService: AdminService, 
    public router: Router
  ) { }

  ngOnInit() {
    this.loading = true;

    this.page = this.adminService.getAdminRoute();
    this.menu_items = this.adminService.getAdminMenu();

    this.globalService.getMaintenances().subscribe((maintenances: any) => {
      this.maintenances = maintenances;

      this.loading = false;
    })
  }

  addMaintenance() {
    this.router.navigate(['admin/maintenances/create']);
  }
}
