import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';
import { GuaranteesService } from 'src/app/Services/guarantees.service';

@Component({
  selector: 'app-guarantee-maintenances',
  templateUrl: './guarantee-maintenances.component.html',
  styleUrls: ['./guarantee-maintenances.component.css']
})
export class GuaranteeMaintenancesComponent implements OnInit {

  public page: any = {};
  public groups: any = [];
  public pattern: any = {};
  public data: any = {};
  public menu_items: any = [];
  public loading: boolean = false;
  public guarantee_maintenance: any = '';

  constructor(
    private globalService: GlobalService,
    private adminService: AdminService,
    private guaranteeService: GuaranteesService,
    public router: Router
  ) { }

  async ngOnInit() {
    this.page = this.adminService.getAdminRoute();
    this.menu_items = this.adminService.getAdminMenu();

    this.guarantee_maintenance = await this.guaranteeService.getGuaranteeMaintenance()

    this.getGuaranteeMaintenances(this.guarantee_maintenance);
  }

  getGuaranteeMaintenances(guarantee) {
    this.globalService.getRootGuaranteesMaintenances(guarantee).subscribe((data: any) => {
      console.log(data);
      
      this.data = data;
    });
  }

  addMaintenance() {
    let header = {
      guarantee: this.data.guarantee_description,
      group: this.data.root_guarantee_group_description,
      item: this.data.root_guarantee_item_description
    }

    this.guaranteeService.setHeaderGuaranteeMaintenance(header);

    this.router.navigate(['admin/guarantee-maintenances/create']);
  }

}
