import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-admin-show-companies',
  templateUrl: './admin-show-companies.component.html',
  styleUrls: ['./admin-show-companies.component.css']
})
export class AdminShowCompaniesComponent implements OnInit {

  public page: any = {};
  public companies: any;
  public menu_items: any = [];
  public loading: boolean = false;

  constructor(
    private globalService: GlobalService,
    private adminService: AdminService,
    public router: Router
  ) { }

  ngOnInit() {
    this.page = this.adminService.getAdminRoute();
    this.menu_items = this.adminService.getAdminMenu();

    this.globalService.getCompanies().subscribe((companies: any) => {
      this.companies = companies;
    });
  }

  addCompany() {
    this.router.navigate(['admin/companies/create']);
  }

}
