import { Component, OnInit } from '@angular/core';

import { AdminService } from 'src/app/Services/admin.service';
import { GlobalService } from 'src/app/Services/global.service';

@Component({
  selector: 'app-admin-create-user',
  templateUrl: './admin-create-user.component.html',
  styleUrls: ['./admin-create-user.component.css']
})
export class AdminCreateUserComponent implements OnInit {

  public page: any = {};
  public menu_items: any = [];
  public roles: any = [];
  public loading: boolean = false;
  public companies: any = [];
  public condominiums: any = [];

  public user: any = {
    role_id: 2,
    username: '',
    email: '',
    password: '',
    id_number: '',
    cep: '',
    address: '',
    complement: '',
    phone_number: '',
    is_active:true,
    company_id: '',
    condominium_id: ''
  }

  constructor(private adminService: AdminService, private globalService: GlobalService) { }

  ngOnInit() {
    this.page = this.adminService.getAdminRoute();
    this.menu_items = this.adminService.getAdminMenu();

    this.getRoles();
    this.getCompanies();
  }

  getRoles() {
    this.globalService.getRoles().subscribe((roles: any) => {
      this.roles = roles;
    });
  }

  getCompanies() {
    this.globalService.getCompanies().subscribe((companies: any) => {
      this.companies = companies;
    });
  }

  getCondominiumsByCompany(company) {
    this.globalService.getCondominiumsByCompany(company).subscribe((condominiums: any) => {
      this.condominiums = condominiums;
    });
  }

  createUser() {
    this.loading = true;

    this.globalService.createUser(this.user).subscribe(data => {
      this.loading = false;
      console.log('asdasdasd'); 
      this.globalService.goBack();
    });
  }
}
