import { ShowGuaranteesPatternComponent } from './pages/admin/admin-guarantees/show-guarantees-pattern/show-guarantees-pattern.component';
import { CreatePatternComponent } from './pages/admin/admin-guarantees/create-pattern/create-pattern.component';
import { EditPatternComponent } from './pages/admin/admin-guarantees/edit-pattern/edit-pattern.component';
import { GuaranteeMaintenancesComponent } from './pages/admin/admin-guarantees/guarantee-maintenances/guarantee-maintenances.component';
import { CreateGuaranteeMaintenanceComponent } from './pages/admin/admin-guarantees/create-guarantee-maintenance/create-guarantee-maintenance.component';
import { ShowMaintenancesComponent } from './pages/admin/admin-maintenances/show-maintenances/show-maintenances.component';
import { CreateMaintenanceComponent } from './pages/admin/admin-maintenances/create-maintenance/create-maintenance.component';
import { ShowMaintenancesPatternComponent } from './pages/admin/admin-maintenances/show-maintenances-pattern/show-maintenances-pattern.component';
import { CreatePatternMaintenanceComponent } from './pages/admin/admin-maintenances/create-pattern-maintenance/create-pattern-maintenance.component';
import { EditMaintenanceComponent } from './pages/admin/admin-maintenances/edit-maintenance/edit-maintenance.component';
import { ShowCondominiumsComponent } from './pages/admin/admin-condominiums/show-condominiums/show-condominiums.component';
import { CreateCondominiumComponent } from './pages/admin/admin-condominiums/create-condominium/create-condominium.component';
import { EditCondominiumComponent } from './pages/admin/admin-condominiums/edit-condominium/edit-condominium.component';
import { ShowGroupsComponent } from './pages/admin/admin-management/groups/show-groups/show-groups.component';
