import { ShowItemsComponent } from './pages/admin/admin-management/items/show-items/show-items.component';
import { CreateItemsComponent } from './pages/admin/admin-management/items/create-items/create-items.component';
import { EditItemsComponent } from './pages/admin/admin-management/items/edit-items/edit-items.component';

/* Company Routes */
import { CompanyDashboardComponent } from './pages/company/company-dashboard/company-dashboard.component';

/* User Routes */
import { UserDashboardComponent } from './pages/user/user-dashboard/user-dashboard.component';

const routes: Routes = [
  { path: 'login', component: LoginComponent },
  { path: 'preinit', component: PreinitComponent },

  /* Admin Routes */
  { path: 'admin/dashboard', component: AdminDashboardComponent },
  { path: 'admin/users', component: AdminShowUserComponent },
  { path: 'admin/users/create', component: AdminCreateUserComponent },
  { path: 'admin/users/:id/edit', component: AdminEditUserComponent },
  { path: 'admin/companies', component: AdminShowCompaniesComponent },
  { path: 'admin/companies/create', component: AdminCreateCompanyComponent },
  { path: 'admin/companies/:id/edit', component: AdminEditCompanyComponent },
  { path: 'admin/guarantees', component: ShowGuaranteesComponent },
  { path: 'admin/guarantees/create', component: CreateGuaranteeComponent },
  { path: 'admin/guarantees/:id/edit', component: EditGuaranteeComponent },
  { path: 'admin/guarantees/pattern', component: ShowGuaranteesPatternComponent },
  { path: 'admin/guarantees/pattern/create', component: CreatePatternComponent },
  { path: 'admin/guarantees/pattern/:id/edit', component: EditPatternComponent },
  { path: 'admin/guarantee-maintenances', component: GuaranteeMaintenancesComponent },
  { path: 'admin/guarantee-maintenances/create', component: CreateGuaranteeMaintenanceComponent },
  { path: 'admin/maintenances', component: ShowMaintenancesComponent },
  { path: 'admin/maintenances/create', component: CreateMaintenanceComponent },
  { path: 'admin/maintenances/pattern', component: ShowMaintenancesPatternComponent },
  { path: 'admin/maintenances/pattern/create', component: CreatePatternMaintenanceComponent },
  { path: 'admin/maintenances/:id/edit', component: EditMaintenanceComponent },
  { path: 'admin/condominiums', component: ShowCondominiumsComponent },
  { path: 'admin/condominiums/create', component: CreateCondominiumComponent },
  { path: 'admin/condominiums/:id/edit', component: EditCondominiumComponent },
  { path: 'admin/groups', component: ShowGroupsComponent },
  { path: 'admin/groups/create', component: CreateGroupsComponent },
  { path: 'admin/groups/:id/edit', component: EditGroupsComponent },
  { path: 'admin/items', component: ShowItemsComponent },
  { path: 'admin/items/create', component: CreateItemsComponent },
  { path: 'admin/items/:id/edit', component: EditItemsComponent },
  { path: 'admin/subitems', component: ShowSubitemsComponent },
  { path: 'admin/subitems/create', component: CreateSubitemsComponent },
  { path: 'admin/subitems/:id/edit', component: EditSubitemsComponent },

  /* Company Routes */
  { path: 'company/dashboard', component: CompanyDashboardComponent },

  /* User Routes */
  { path: 'user/dashboard', component: UserDashboardComponent },

  { path: '', redirectTo: 'login', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
