import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-show-condominiums',
  templateUrl: './show-condominiums.component.html',
  styleUrls: ['./show-condominiums.component.css']
})
export class ShowCondominiumsComponent implements OnInit {

  public page: any = {};
  public condominiums: any;
  public menu_items: any = [];
  public loading: boolean = false;

  constructor(
    private globalService: GlobalService, 
    private adminService: AdminService,
    public router: Router
  ) { }

  ngOnInit() {
    this.loading = true;

    this.page = this.adminService.getAdminRoute();
    this.menu_items = this.adminService.getAdminMenu();

    this.globalService.getCondominiums().subscribe((condominiums: any) => {
      this.condominiums = condominiums;

      this.loading = false;
    });
  }

  addCondominium() {
    this.router.navigate(['admin/condominiums/create']);
  }

}
