import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-create-items',
  templateUrl: './create-items.component.html',
  styleUrls: ['./create-items.component.css']
})
export class CreateItemsComponent implements OnInit {

  public page: any = {};
  public groups: any = [];
  public menu_items: any = [];
  public loading: boolean = false;

  public item: any = {
    group_id: '',
    description: ''
  };

  constructor(
    private globalService: GlobalService,
    private adminService: AdminService,
    public router: Router
  ) { }

  ngOnInit() {
    this.page = this.adminService.getAdminRoute();
    this.menu_items = this.adminService.getAdminMenu();

    this.getGroups();
  }

  getGroups() {
    this.globalService.getGroups().subscribe((groups: any) => {
      this.groups = groups;
    });
  }

  addItem() {
    this.loading = true;

    this.globalService.createItem(this.item).subscribe(data => {
      this.globalService.goBack();

      this.loading = false;
    });
  }

}
