import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { AdminService } from 'src/app/Services/admin.service';
import { GlobalService } from 'src/app/Services/global.service';

@Component({
  selector: 'app-admin-show-user',
  templateUrl: './admin-show-user.component.html',
  styleUrls: ['./admin-show-user.component.css']
})
export class AdminShowUserComponent implements OnInit {

  public page: any = {};
  public users: any = [];
  public menu_items: any = [];

  constructor(
    private adminService: AdminService,
    private globalService: GlobalService, 
    public router: Router
  ) { }

  ngOnInit() {
    this.page = this.adminService.getAdminRoute();
    this.menu_items = this.adminService.getAdminMenu();

    this.globalService.getUsers().subscribe((users: any) => {
      this.users = users;
    });
  }

  addUser() {
    this.router.navigate(['admin/users/create']);
  }

  addAttributes(user) {
    this.adminService.setUser(user);

    this.router.navigate(['admin/users/add-attribute']);
  }

}
