import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-show-groups',
  templateUrl: './show-groups.component.html',
  styleUrls: ['./show-groups.component.css']
})
export class ShowGroupsComponent implements OnInit {

  public page: any = {};
  public menu_items: any = [];
  public groups: any = [];

  constructor(
    private globalService: GlobalService,
    private adminService: AdminService,
    public router: Router
  ) { }

  ngOnInit() {
    this.page = this.adminService.getAdminRoute();
    this.menu_items = this.adminService.getAdminMenu();

    this.globalService.getGroups().subscribe((groups: any) => {
      this.groups = groups;
    });
  }

  addGroup() {
    this.router.navigate(['admin/groups/create']);
  }

}
