import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { GlobalService } from 'src/app/Services/global.service';

@Component({
  selector: 'app-preinit',
  templateUrl: './preinit.component.html',
  styleUrls: ['./preinit.component.css']
})
export class PreinitComponent implements OnInit {

  constructor(private globalService: GlobalService, public router: Router) { }

  ngOnInit() {
    this.getLoggedUser();
  }

  getLoggedUser() {
    this.globalService.getLoggedUser().subscribe((user: any) => {
      localStorage.setItem('username', user.username)
      localStorage.setItem('role', user.role.description);

      if (user.role.description === 'Admin') {
        this.router.navigate(['admin/dashboard'])
      }

      if (user.role.description === 'Company') {
        this.router.navigate(['company/dashboard'])
      }

      if (user.role.description === 'User') {
        this.router.navigate(['user/dashboard'])
      }
    });
  }

}
