import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-admin-create-company',
  templateUrl: './admin-create-company.component.html',
  styleUrls: ['./admin-create-company.component.css']
})
export class AdminCreateCompanyComponent implements OnInit {

  public page: any = {};
  public menu_items: any = [];
  public loading: boolean = false;

  public company: any = {
    name: '',
    cnpj: '',
    cep: '',
    address: '',
    complement: '',
    website: '',
    email: '',
    phone_number: '',
    responsible: '',
  }

  constructor(
    private globalService: GlobalService, 
    private adminService: AdminService,
    public router: Router
  ) { }

  ngOnInit() {
    this.page = this.adminService.getAdminRoute();
    
    this.menu_items = this.adminService.getAdminMenu();
  }

  createCompany() {
    this.loading = true;

    this.globalService.createCompany(this.company).subscribe(data => {
      this.loading = false;
      
      this.router.navigate(['admin/companies']);
    });
  }

}
