import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-show-maintenances-pattern',
  templateUrl: './show-maintenances-pattern.component.html',
  styleUrls: ['./show-maintenances-pattern.component.css']
})
export class ShowMaintenancesPatternComponent implements OnInit {

  public page: any = {};
  public groups: any = [];
  public maintenances: any = [];
  public menu_items: any = [];
  public loading: boolean = false;

  constructor(
    private globalService: GlobalService,
    private adminService: AdminService,
    public router: Router
  ) { }

  ngOnInit() {
    this.loading = true;

    this.page = this.adminService.getAdminRoute();
    this.menu_items = this.adminService.getAdminMenu();

    this.getMaintenances();
  }

  getMaintenances() {
    this.globalService.getMaintenances().subscribe((maintenances: any) => {
      this.maintenances = maintenances;
    });
  }

  addMaintenance() {
    this.router.navigate(['admin/maintenances/pattern/create']);
  }
}
