import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class GuaranteesService {

  public pattern_guarantee: any = {};
  public guarantee_maintenance: any = {};
  public header_guarantee_maintenance: any = {};

  constructor() { }

  setPatternGuarantee(pattern) {
    this.pattern_guarantee = pattern;
  }

  getPatternGuarantee() {
    return this.pattern_guarantee;
  }

  setGuaranteeMaintenance(guarantee) {
    this.guarantee_maintenance = guarantee;
  }

  getGuaranteeMaintenance() {
    return this.guarantee_maintenance;
  }

  setHeaderGuaranteeMaintenance(header) {
    this.header_guarantee_maintenance = header;
  }

  getHeaderGuaranteeMaintenance() {
    return this.header_guarantee_maintenance;
  }
}
