import { Component, OnInit } from '@angular/core';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-create-pattern',
  templateUrl: './create-pattern.component.html',
  styleUrls: ['./create-pattern.component.css']
})
export class CreatePatternComponent implements OnInit {

  public guarantee: any = {
    name: '',
    description: ''
  }

  public page: any = {};
  public menu_items: any = [];
  public loading: boolean = false;

  constructor(private globalService: GlobalService, private adminService: AdminService) { }

  ngOnInit() {
    this.page = this.adminService.getAdminRoute();

    this.menu_items = this.adminService.getAdminMenu();
  }

  addGuaranteePattern() {
    this.loading = true;

    this.globalService.createGuaranteePattern(this.guarantee).subscribe((data: any) => {
      this.globalService.goBack();

      this.loading = false;
    });
  }

}
