import { Component, OnInit } from '@angular/core';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-create-pattern-maintenance',
  templateUrl: './create-pattern-maintenance.component.html',
  styleUrls: ['./create-pattern-maintenance.component.css']
})
export class CreatePatternMaintenanceComponent implements OnInit {

  public maintenance: any = {
    name: '',
    description: ''
  }

  public page: any = {};
  public menu_items: any = [];
  public loading: boolean = false;

  constructor(private globalService: GlobalService, private adminService: AdminService) { }

  ngOnInit() {
    this.page = this.adminService.getAdminRoute();

    this.menu_items = this.adminService.getAdminMenu();
  }

  addMaintenancePattern() {
    this.loading = true;

    this.globalService.createMaintenancePattern(this.maintenance).subscribe((data: any) => {
      this.globalService.goBack();

      this.loading = false;
    });
  }

}
