import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-create-groups',
  templateUrl: './create-groups.component.html',
  styleUrls: ['./create-groups.component.css']
})
export class CreateGroupsComponent implements OnInit {

  public page: any = {};
  public menu_items: any = [];
  public loading: boolean = false;
  public group: any = {
    description: ''
  };

  constructor(
    private globalService: GlobalService,
    private adminService: AdminService,
    public router: Router
  ) { }

  ngOnInit() {
    this.page = this.adminService.getAdminRoute();

    this.menu_items = this.adminService.getAdminMenu();
  }

  addGroup() {
    this.loading = true;

    this.globalService.createGroup(this.group).subscribe(data => {
      this.globalService.goBack();

      this.loading = false;
    });
  }

}
