import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-show-guarantees-pattern',
  templateUrl: './show-guarantees-pattern.component.html',
  styleUrls: ['./show-guarantees-pattern.component.css']
})
export class ShowGuaranteesPatternComponent implements OnInit {

  public page: any = {};
  public groups: any = [];
  public guarantees: any = [];
  public menu_items: any = [];
  public loading: boolean = false;

  constructor(
    private globalService: GlobalService, 
    private adminService: AdminService,
    public router: Router
  ) { }

  ngOnInit() {
    this.loading = true;

    this.page = this.adminService.getAdminRoute();
    this.menu_items = this.adminService.getAdminMenu();

    this.getGuarantees();
  }

  getGuarantees() {
    this.globalService.getGuarantees().subscribe((guarantees: any) => {
      this.guarantees = guarantees;
    });
  }

  addGuarantee() {
    this.router.navigate(['admin/guarantees/pattern/create']);
  }

}
