import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';
import { GuaranteesService } from 'src/app/Services/guarantees.service';

@Component({
  selector: 'app-show-guarantees',
  templateUrl: './show-guarantees.component.html',
  styleUrls: ['./show-guarantees.component.css']
})
export class ShowGuaranteesComponent implements OnInit {

  public guarantee: any;

  public page: any = {};
  public groups: any = [];
  public guarantees: any = [];
  public menu_items: any = [];
  public loading: boolean = false;
  public root_guarantees: any = [];

  constructor(
    private globalService: GlobalService,
    private adminService: AdminService,
    private guaranteeService: GuaranteesService,
    public router: Router
  ) { }

  ngOnInit() {
    this.loading = true;

    this.page = this.adminService.getAdminRoute();
    this.menu_items = this.adminService.getAdminMenu();

    this.getGuarantees();
  }

  getGuarantees() {
    this.globalService.getGuarantees().subscribe((guarantees: any) => {
      this.guarantees = guarantees;
    });
  }

  async getRootGuarantees(element) {
    let guarantee = {
      guarantee_id: element
    }

    await this.guaranteeService.setPatternGuarantee(guarantee);

    this.globalService.getRootGuarantees(guarantee).subscribe((guarantees: any) => {
      this.root_guarantees = guarantees;
    });
  }

  addGuarantee() {
    this.router.navigate(['admin/guarantees/create']);
  }

  collapse(group) {
    group.collapse = !group.collapse;
  }

  async guaranteeMaintenance(element) {
    let guarantee = {
      root_guarantee_id: element
    }

    await this.guaranteeService.setGuaranteeMaintenance(guarantee);

    this.router.navigate(['admin/guarantee-maintenances']);
  }

}
