import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-create-condominium',
  templateUrl: './create-condominium.component.html',
  styleUrls: ['./create-condominium.component.css']
})
export class CreateCondominiumComponent implements OnInit {

  public page: any = {};
  public menu_items: any = [];
  public companies: any = [];
  public loading: boolean = false;

  public condominium: any = {
    company_id: '',
    file_id: '',
    name: '',
    id_number: '',
    address: '',
    complement: '',
    zipcode: '',
    licence_due_date: '',
  }

  constructor(
    private globalService: GlobalService, 
    private adminService: AdminService,
    public router: Router
  ) { }

  ngOnInit() {
    this.page = this.adminService.getAdminRoute();
    this.menu_items = this.adminService.getAdminMenu();

    this.globalService.getCompanies().subscribe((companies: any) => {
      this.companies = companies;
    });
  }

  createCondominium() {
    this.loading = true;

    this.globalService.createCondominium(this.condominium).subscribe(data => {
      this.loading = false;
      
      this.router.navigate(['admin/condominiums']);
    });
  }

}
