import { Component, OnInit } from '@angular/core';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-show-subitems',
  templateUrl: './show-subitems.component.html',
  styleUrls: ['./show-subitems.component.css']
})
export class ShowSubitemsComponent implements OnInit {

  public page: any = {};
  public menu_items: any = [];
  public subitems: any = [];

  constructor(
    private globalService: GlobalService,
    private adminService: AdminService,
    public router: Router
  ) { }

  ngOnInit() {
    this.page = this.adminService.getAdminRoute();
    this.menu_items = this.adminService.getAdminMenu();

    this.globalService.getSubitems().subscribe((subitems: any) => {
      this.subitems = subitems;
    });
  }

  addSubitem() {
    this.router.navigate(['admin/subitems/create']);
  }

}
