import { Component, OnInit } from '@angular/core';

import { GlobalService } from 'src/app/Services/global.service';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-create-maintenance',
  templateUrl: './create-maintenance.component.html',
  styleUrls: ['./create-maintenance.component.css']
})
export class CreateMaintenanceComponent implements OnInit {

  public page: any = {};
  public items: any = [];
  public groups: any = [];
  public menu_items: any = [];
  public guarantees: any = [];
  public loading: boolean = false;

  public maintenance: any = {
    name: '',
    description: ''
  }

  constructor(private globalService: GlobalService, private adminService: AdminService) { }

  ngOnInit() {
    this.page = this.adminService.getAdminRoute();

    this.menu_items = this.adminService.getAdminMenu();
  }

}
